<template>
	<YPage :logo="require('@/assets/img/logo_white.png')">
		<div>
			<!-- <YMenuTab :list="tabList" :current="current" @change="handleTabChange"/> -->
			<div class="yu-page-container s-article-index">
				<YPanel v-for="(item,index) in list" :key="index" :url="item.url" :src="item.img" :title="item.title" :desc="item.desc" @click="handlePanel(item)" />
			</div>
		</div>
	</YPage>
</template>

<script>
	import YMenuTab from "@/components/YMenuTab"
	import YPanel from "@/components/YPanel"
	import newsList from "@/mock/newsList"
	export default {
		components: {
			YMenuTab,
			YPanel
		},
		data() {
			return {
				tabList: [],
				current: 0,
				list: []
			}
		},
		mounted() {
			this.list = newsList;
		},
		methods: {
			handleTabChange() {},
			handlePanel(obj) {
				// window.location.href = obj.url;
				window.open(obj.url,"_blank");
			}
		}
	}
</script>

<style lang="scss" scoped>
	.s-article-index {
		padding-bottom: 10px;
	}
</style>