<template>
  <div ref="image" class="yu-img-wrapper" :class="{'yu-img-aspect-fill':mode === 'aspectFill'}"
       :style="{borderRadius:radius + 'px'}">
    <img class="img" :style="{width:imgWidth,height:imgHeight}" :src="imgSrc" alt="" @load="bindImgInfo"
         @error="bindImgError"/>
  </div>
</template>

<script>
const debounce = (func, delay)=> {
  let timer;
  return function () {
    clearTimeout(timer);
    timer = setTimeout(function () {
      func();
    }, delay);
  };
}
export default {
  name: 'YtImg',
  props: {
    src: {
      type: String,
      default() {
        return ''
      }
    },
    radius: {
      type: [Number, String],
      default() {
        return 0
      }
    },
    /**  mode:图片的裁剪模式
     scaleToFill:缩放模式，不保持纵横比缩放图片，使图片的宽高完全拉伸至填满
     widthFix: 默认值，缩放模式，宽度不变，高度自动变化，保持原图宽高比不变
     heightFix:	缩放模式，高度不变，宽度自动变化，保持原图宽高比不变
     aspectFill:缩放模式，保持纵横比缩放图片，只保证图片的短边能完全显示出来。也就是说，图片通常只在水平或垂直方向是完整的，另一个方向将会发生截取。
     **/
    mode: {
      type: String,
      default() {
        return 'scaleToFill'
      }
    },
    /**
     width,height :图片显示区域宽高
     单位可以是px、%、vw、vh，mode=“aspectFill”时建议单位使用px，其他值会监听视窗变化消耗比较大
     **/
    width: {
      type: String,
      default() {
        return ''
      }
    },
    height: {
      type: String,
      default() {
        return ''
      }
    }
  },
  data() {
    return {
      imgSrc: '',
      imgWidth: '100%',
      imgHeight: '100%',
      imgRatio: 1,
      fn:debounce(this.bindSetAspectFill,3000)
    }
  },
  watch: {
    'src': {
      handler: function (newVal) {
        if (newVal) {
          this.imgSrc = newVal;
        }
      },
      immediate: true
    }
  },
  mounted() {

  },
  methods: {
    debounce(func, delay) {
      let timer;
      return function () {
        clearTimeout(timer);
        timer = setTimeout(function () {
          func();
        }, delay);
      };
    },
    bindSetAspectFill() {
      let element = this.$refs.image;
      let initWidth = element.getBoundingClientRect().width, initHeight = element.getBoundingClientRect().height;
      let ratio = (initHeight && initWidth) ? initHeight / initWidth : 1;
      console.log(this.imgRatio, ratio, initWidth, initHeight)
      if (this.imgRatio >= ratio) {
        this.imgWidth = '100%';
        this.imgHeight = 'auto';
      } else {
        this.imgWidth = 'auto';
        this.imgHeight = '100%';
      }
    },
    bindImgInfo(e) {
      let img = new Image();
      img.src = e.target.src;
      let el = this.$refs.image;
      let imgWidth = img.naturalWidth, imgHeight = img.naturalHeight;
      let w = 320, h = 240;
      let reg = /^\d+(\.\d+)?(%|px|vw|vh)$/;
      if (this.mode === 'widthFix') {
        el.style.width = !reg.test(this.width) ? `${w}px` : this.width;
        el.style.height = 'auto';
      } else if (this.mode === 'heightFix') {
        el.style.width = 'auto';
        el.style.height = !reg.test(this.height) ? `${h}px` : this.height;
      } else if (this.mode === 'aspectFill') {
        let imgRatio = imgHeight / imgWidth;
        if (!reg.test(this.width) || !reg.test(this.height)) {
          let ratio = h / w;
          el.style.width = `${w}px`;
          el.style.height = `${h}px`;
          if (imgRatio >= ratio) {
            this.imgWidth = '100%';
            this.imgHeight = 'auto';
          } else {
            this.imgWidth = 'auto';
            this.imgHeight = '100%';
          }
        } else {
          el.style.width = this.width;
          el.style.height = this.height;
          this.imgRatio = imgRatio;
          this.bindSetAspectFill();
          if (this.width.indexOf('px') === -1 || this.height.indexOf('px') === -1) {
            window.addEventListener('resize', this.fn)
          }
        }
      } else {
        if (!reg.test(this.width) || !reg.test(this.height)) {
          el.style.width = `${w}px`;
          el.style.height = `${h}px`;
        } else {
          el.style.width = this.width;
          el.style.height = this.height;
        }
      }
    },
    bindImgError() {
      this.imgSrc = `data:image/png;base64,`;
    }
  },
  destroyed() {
    window.removeEventListener('resize', this.fn);
  }
}
</script>
<style scoped>
.yu-img-wrapper {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  line-height: 0;
}

.yu-img-wrapper .img {
  max-width: none;
}

.yu-img-aspect-fill {
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
</style>
