<template>
	<div class="yu-panel-wrapper" :class="'yu-panel-' + align" @click="onClick">
		<div class="yu-panel-hd" v-if="src">
			<YImg :src="src"  mode="aspectFill" :width="imgWidth" :height="imgHeight" :radius="5"></YImg>
		</div>
		<div class="yu-panel-bd">
			<div class="title" :style="{fontSize:titleSize + 'px',color:titleColor}" v-text="title"></div>
			<p class="desc" v-if="desc" v-html="desc"></p>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	import YImg from "./YImg"
	export default {
		name: 'YPanel',
		components: {
			YImg
		},
		props: {
			title: {
				type: String,
				default () {
					return ''
				}
			},
			titleSize: {
				type: [Number, String],
				default () {
					return 16
				}
			},
			titleColor: {
				type: String,
				default () {
					return '#333'
				}
			},
			desc: {
				type: String,
				default () {
					return ''
				}
			},
			align: {
				type: String,
				default () {
					return 'left'
				}
			},
			src: {
				type: String,
				default () {
					return ''
				}
			},
			imgWidth: {
				type: String,
				default () {
					return '120px'
				}
			},
			imgHeight: {
				type: String,
				default () {
					return '90px'
				}
			},
			url: {
				type: String,
				default () {
					return ''
				}
			},
		},
		data() {
			return {

			}
		},
		watch: {

		},
		methods: {
			onClick(){
				this.$emit('click')
			}
		}
	}
</script>
<style lang="scss" scoped>
	.yu-panel-left {
		.yu-panel-hd {
			margin-right: 15px;
		}
	}

	.yu-panel-right {
		flex-direction: row-reverse;

		.yu-panel-hd {
			margin-left: 15px;
		}
	}
	.yu-panel-wrapper{
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		cursor: pointer;
		background-color: #fff;
		border-radius: 8px;
		padding: 20px;
		margin: 12px 0;
		.yu-panel-hd{
			font-size: 0;
			line-height: 0;
		}
		.yu-panel-bd{
			-webkit-flex: 1;
			-ms-flex: 1;
			-webkit-box-flex: 1;
			-moz-box-flex: 1;
			flex: 1;
			.title{
				font-size: 15px;
				line-height: 24px;
				font-weight: 500;
				padding: 3px 0;
			}
			.desc{
				padding: 2px 0;
				font-size: 12px;
				line-height: 20px;
				color: #999;
			}
		}
	}
</style>
